* {
  padding: 0;
  margin: 0;
}

.full-edit-mode header {
  display: none;
}
.full-edit-mode .app-wrapper {
  margin: 0 auto;
}
.full-edit-mode .app-wrapper.wrapper-min.sidebar-fixed .sidebar-container {
  top: 0;
}

.navbar {
  font-size: 1.5rem;
}
.navbar > li {
  list-style: none;
  display: inline-block;
  width: 3rem;
  padding: 5px 10px;
}
.navbar > li .active > a {
  color: #fff;
  background: #454545;
}
.navbar > li .down {
  display: none;
  position: absolute;
  border: 1px solid #767766;
  background: #fff;
  color: #333;
  z-index: 9999;
}
.navbar > li .down > li {
  border-bottom: 1px solid #767766;
  padding: 0 5px 0 30px;
  position: relative;
  cursor: pointer;
}
.navbar > li .down > li:hover {
  color: #fff;
  background: #7e8;
}
.navbar > li .down > li i {
  margin-right: 1rem;
  position: absolute;
  left: 10px;
  line-height: 30px;
}
.navbar > li.open .down {
  display: block;
}
.navbar > li > div,
.navbar > li > a {
  text-align: center;
  text-decoration: none;
  color: #fff;
}
.navbar > li > div span,
.navbar > li > a span {
  display: block;
  font-size: 0.7rem;
}
.navbar.min > li > div span {
  display: none;
}

.top-nav {
  background: #767766;
  color: #fff;
  margin: -8px -8px 0 0;
}
.top-nav .navbar {
  font-size: 1.5rem;
}

.card {
  width: 150px;
  height: 200px;
  float: left;
  margin: 5px;
  position: relative;
  border: 1px solid;
  overflow: hidden;
}
.card .card-logo img {
  width: 150px;
  height: 200px;
}
.card .card-body {
  position: absolute;
  bottom: 0;
  left: 0;
  right: 0;
  min-height: 80px;
  background: rgba(255, 255, 255, 0.6);
}
.card .card-body h3 {
  text-align: center;
}
.card .card-action {
  display: none;
  position: absolute;
  top: 0;
  right: 0;
}
.card.card-add .card-logo {
  border-radius: 50%;
  border: 1px solid rgba(0, 0, 0, 0.1);
  width: 100px;
  height: 100px;
  margin: 50px auto 0;
  text-align: center;
  font-size: 75px;
}
.card.card-add:hover .card-logo {
  border: 1px solid rgba(56, 243, 250, 0.1);
  color: #fcc;
}
.card:hover {
  border: 1px solid burlywood;
}
.card:hover .card-body {
  background: rgba(255, 255, 255, 0.8);
}
.card:hover .card-action {
  display: block;
}

.theme-select .theme-item,
.theme-select .page-item,
.page-select .theme-item,
.page-select .page-item {
  display: inline-block;
  text-align: center;
  margin: 5px;
}
.theme-select .theme-item .thumb,
.theme-select .page-item .thumb,
.page-select .theme-item .thumb,
.page-select .page-item .thumb {
  height: 80px;
  width: 80px;
}
.theme-select .theme-item .thumb img,
.theme-select .page-item .thumb img,
.page-select .theme-item .thumb img,
.page-select .page-item .thumb img {
  width: 100%;
  height: 100%;
}
.theme-select .theme-item.active,
.theme-select .page-item.active,
.page-select .theme-item.active,
.page-select .page-item.active {
  border: 1px solid red;
}
.theme-select .theme-item:hover,
.theme-select .page-item:hover,
.page-select .theme-item:hover,
.page-select .page-item:hover {
  box-shadow: 3px 3px 4px rgba(33, 33, 33, 0.7);
}

.theme-select {
  max-height: 600px;
  overflow-y: auto;
}

.theme-dialog .dialog-body {
  overflow: unset;
}

.dialog-body-box {
  overflow-y: auto;
  background-color: #eee;
  max-width: 600px;
  max-height: 400px;
}
.dialog-body-box .item {
  text-align: center;
  display: inline-block;
  background-color: #fff;
  margin: 3px;
  position: relative;
}
.dialog-body-box .item .thumb {
  width: 100px;
  height: 100px;
}
.dialog-body-box .item .thumb img {
  width: 100%;
  height: 100%;
}
.dialog-body-box .item .name {
  width: 100px;
  overflow: hidden;
  color: #333;
  font-size: 12px;
  height: 16px;
}
.dialog-body-box .item.selected {
  background-color: rgb(174, 255, 238);
}
.dialog-body-box .item.selected::after {
  position: absolute;
  font-size: 100px;
  color: rgba(255, 0, 0, 0.3);
  font-family: "Font Awesome 5 Free";
  content: "\f00c";
  font-weight: 900;
  top: 10px;
  left: 0;
}
.dialog-body-box .dialog-pager {
  text-align: center;
}

.custom-config-view .multiple-box .item {
  border: 1px dotted #ccc;
  margin-bottom: 5px;
  position: relative;
}
.custom-config-view .multiple-box .item .multiple-del-btn {
  position: absolute;
  top: 0;
  right: 0;
  width: 20px;
  height: 20px;
  border-radius: 50%;
  background-color: red;
  color: white;
  line-height: 20px;
  text-align: center;
}

.custom-config-view {
  max-height: 80vh;
}